


































import mInput from '../../components/m-input.vue'
export default {
	components: {
	    mInput
	},
	data() {
		return {
			TabCur: 0,
			scrollLeft: 0,
		
			ypassword:"",
			npassword:"",
			repassword:"",
			
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			menuBorder: false,
			menuArrow: false,
			menuCard: false,
			disabled: false,
			isdisabled:true,
		};
	},
	onLoad() {
		const value = uni.getStorageSync('agentInfo');
		if (value) {
			console.log(value);
			this.id=value.id;
			this.tel=value.agent_tel
		}
	},
	methods: {
		navTo:function(){
			uni.redirectTo({
				url: '../tabbar/tabbar'
			});
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 80
		},
		editpwd:function(){
			if (this.ypassword == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入原密码'
				});
				return;
			} else if (this.npassword== '') {
				uni.showToast({
					icon: 'none',
					title: '请输入新密码'
				});
				return;
			}else if (this.repassword== '') {
				uni.showToast({
					icon: 'none',
					title: '请重新输入新密码'
				});
				return;
			}
			uni.request({
				url: 'http://192.168.0.199:8080/agent/***/***',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				method: 'POST',
				dataType: 'json',
				cache: false,
				data: {
					token:this.token,
					ypassword:this.ypassword,
					npassword:this.npassword,
					repassword:this.repassword,
				},
				success: res => {
					
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				},
				complete: () => {}
				});
			
			
			
			
		}
		
	}
}
